// 热门影视评分星星
import hotStars from '../../components/hotStars'
export default {
	data() {
		return {
			// 轮播图数据
			swiperList: [],
			// 热门影视
			moveHotList: [],
			// 影视预告片
			trailerList: [],
			// 猜你喜欢
			guessLikeList: [],
			// 动画对象 里面可以放多个动画
			animationData: {},
			animationDataArr: [{}, {}, {}, {}, {}]
		}
	},
	// 组件
	components: {
		hotStars
	},
	onUnload() {
		// 页面卸载时  清除动画数据
		this.animationData = {},
			this.animationDataArr = [{}, {}, {}, {}, {}];
	},
	onLoad() {
		// 页面加载时 渲染轮播图
		this.getSwiperList()
		// 渲染热门影视数据
		this.getMoveHot()
		// 渲染热门预告片
		this.getTrailer()
		// 渲染猜你喜欢
		this.getGuessLike()
		// 创建一个动画实例
		this.animation = uni.createAnimation()
	},
	// 开始下拉刷新
	onPullDownRefresh() {
		// 重新渲染猜你喜欢
		this.getGuessLike()
	},

	// 隐藏
	onHide() {
		if (this.videoContext) {
			this.videoContext.pause();
		}
	},
	methods: {
		// 获取轮播图数据
		getSwiperList() {
			uni.request({
				url: `${this.$api}/index/carousel/list?qq=122212489`,
				method: "POST",
				success: (res) => {
					console.log(res.data.data)
					if (res.data.status == 200) {
						this.swiperList = res.data.data
					}
				}
			});
		},
		// 去详情页面
		goDetail(id) {
			uni.navigateTo({
				url: `/pages/detail/detail?id=${id}`,
			});
		},
		// 获取热门影视数据
		getMoveHot() {
			uni.request({
				url: `${this.$api}/index/movie/hot?qq=122212489&type=superhero`,
				method: "POST",
				success: (res) => {
					console.log(res.data.data)
					this.moveHotList = res.data.data
				}
			})
		},
		// 获取影视预告片数据
		getTrailer() {
			uni.request({
				url: `${this.$api}/index/movie/hot?qq=122212489&type=trailer`,
				method: "POST",
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 200) {
						this.trailerList = res.data.data
					}
				}
			})
		},
		// 播放当前视频  暂停其他的视频
		controlPlay(e) {
			// 播放视频的id
			let trailerId = "";
			if (e) {
				// 用户点击的id
				trailerId = e.currentTarget.dataset.playingindex;
				this.videoContext = uni.createVideoContext(trailerId);
			}

			let trailerList = this.trailerList;
			for (let i = 0; i < trailerList.length ; i ++) {
				let tempId = trailerList[i].id;
				
				// 拿到预告片视频的id 与 用户点击的id对比
				if (tempId != trailerId) {
					uni.createVideoContext(tempId).pause();
				}
			}
		},
		// 猜你喜欢
		getGuessLike() {
			// 页面显示loading加载中
			uni.showLoading({
			    mask: true  // 刷新时 防止触摸穿透 防止用户点击播放视频
			});
			uni.showNavigationBarLoading();  // 当前页面导航栏刷新动画
			uni.request({
				url: `${this.$api}/index/guessULike?qq=122212489`,
				method: "POST",
				success: (res) => {
					console.log(res.data.data)
					if (res.data.status == 200) {
						this.guessLikeList = res.data.data
					}
				},
				// 接口调用结束的回调函数 complete
				complete: () => {
					setTimeout(function () {
						// 停止刷新
						uni.stopPullDownRefresh();
						// 隐藏loading加载中
					    uni.hideLoading();
						// 隐藏导航栏刷新动画
						uni.hideNavigationBarLoading()
					}, 1000);
				}
			})
		},
		// 赞一下 +1 动画
		good(e) {
			var gIndex = e.currentTarget.dataset.gindex;
			console.log(gIndex);
			// return
			// step 一组动画完成后 才能进行下一组
			this.animation.translateY(-60).opacity(1).step({
				// 动画延迟时间
				duration: 400,
			})
			// 导出动画数据
			// this.animationData = this.animation.export()
			this.animationData = this.animation
			this.animationDataArr[gIndex] = this.animationData.export()

			// 还原动画
			setTimeout(() => {
				this.animation.translateY().opacity(0).step({
					duration: 0
				})
				// 导出动画数据
				// this.animationData = this.animation.export()
				this.animationData = this.animation
				this.animationDataArr[gIndex] = this.animationData.export()
			}, 500)
		}
	}
}
